<template>
  <div class="Main-body">
    <div class="Main-body-l">
      <!-- 背景图头部 -->
      <div class="Main-box"></div>
      <div class="Main-top">
        <div class="top1" @click="lwh_MyInfo()">
          <van-image  v-if="lwh_token==''"
            class="lwh_img_head"
            round
            width="0.58rem"
            height="0.58rem"
            :src="lwh_head_cont.avatar"
          />
          <van-image v-if="lwh_token !=''"
            class="lwh_img_head"
            round
            width="0.58rem"
            height="0.58rem"
            :src="lwh_head_cont.avatar"
          />
          <div class="top-div">
            <span>{{ lwh_head_cont.nickname }}</span>
            <p><van-icon size="18" color="#f2995a" name="edit" /></p>
          </div>
          <div class="lwh_text" @click.stop="onClickyk">去约课</div>
        </div>
        <div class="top2">
          <div @click.stop="onClickfeature">
            <span>{{ lwh_head_list.courses }}</span>
            <p>我的特色课</p>
            <p style="color: gray">已购特色课程的学习</p>
          </div>
          <p></p>
          <div @click="onClickcoach">
            <span>{{ lwh_head_list.oto }}</span>
            <p>一对一辅导</p>
            <p style="color: gray">我的一对一老师辅导</p>
          </div>
          <p></p>
          <div @click="onClickstudy">
            <span>{{ lwh_head_list.integral }}</span>
            <p>剩余学习币</p>
            <p style="color: gray">查看剩余学习币</p>
          </div>
        </div>
        <!-- 去约课小图标 -->
      </div>
      <!-- 结束 -->
      <!-- 邀请好友注册app -->
      <div class="Main-app" @click="popup">
        <div class="Main-app-img">
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </div>
        <div class="Main-app-text">
          <p>邀请好友注册APP，享多重好礼<br />现时特惠，多邀多得</p>
        </div>
        <div class="Main-app-left">
          <van-icon name="arrow" />
        </div>
      </div>
      <!-- 弹出层 -->
      <van-popup v-model="show"
        ><img
          class="popus"
          src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2020OPv5ksBfFx1603875973.png"
          alt=""
      /></van-popup>

      <!-- 结束 -->
      <!-- 课程相关 -->
      <div class="Main-k">
        <div class="list_title">
          <p class="lwh_text_p">课程相关</p>
        </div>
        <van-grid icon-size="24" :border="false">
          <van-grid-item
            icon="data:image/png;base64,"
            text="关注的老师"
            @click="onlickcontact"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="我的收藏"
            @click="onlicksc"
          />
        </van-grid>
        <p class="lwh_bor_p"></p>

        <!-- 订单相关 -->
        <div class="list_title">
          <p class="lwh_text_p">订单相关</p>
        </div>
        <van-grid icon-size="24" :border="false">
          <van-grid-item
            icon="data:image/png;base64,"
            text="课程订单"
            @click="onlickKc"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="会员订单"
            @click="onlickHy"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="约客订单"
            @click="onlickYk"
          />
        </van-grid>
        <p class="lwh_bor_p"></p>
        <!-- 我的账户 -->
        <div class="list_title">
          <p class="lwh_text_p">我的账户</p>
        </div>
        <van-grid icon-size="24" :border="false">
          <van-grid-item
            icon="data:image/png;base64,"
            text="优惠卷"
            @click="onDiscount"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="学习卡"
            @click="toCard()"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="会员"
            @click='toVip()' 
          />
        </van-grid>
        <p class="lwh_bor_p"></p>

        <!-- 自助服务 -->
        <div class="list_title">
          <p class="lwh_text_p">自助服务</p>
        </div>
        <van-grid icon-size="24" :border="false">
          <van-grid-item
            icon="data:image/png;base64,"
            text="我的消息"
             @click='toMessage()'
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="意见反馈"
             @click='toFeedback()'
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="在线客服"
             @click="showLeave()"
          />
          <van-grid-item
            icon="data:image/png;base64,"
            text="设置"
            @click="onSetting"
          />
        </van-grid>
      </div>
    </div>
  </div>
</template>

<script>
import BetterScroll from "better-scroll";

export default {
  data() {
    return {
      lwh_bs: null,
      show: false,
      lwh_head_cont: [],
      lwh_head_list: [],
      lwh_token:window.localStorage.getItem("token") || ""
    };
  },
  mounted() {
    // 滑动插件
    this.$nextTick(() => {
      this.lwh_bs = new BetterScroll(".Main-body", {
        probeType: 3,
        click: true,
      });
      // 上滑禁止
      this.lwh_bs.on("scroll", (pos) => {
        if (pos.y >= 0) {
          // console.log(pos.y);
          this.lwh_bs.scrollTo(0, 0);
        }
      });
    });
    this.lwh_Ajax();
    this.lwh_AjaxInfo();
  },
  methods: {
    // 数据获取
    async lwh_Ajax() {
      let { data } = await this.$http.get(
        "https://www.365msmk.com/api/app/userInfo?"
      );
      this.lwh_head_cont = data.data;
      console.log(this.lwh_head_cont);
    },
    // 特色课数据获取
    async lwh_AjaxInfo() {
      let { data } = await this.$http.get(
        "https://www.365msmk.com/api/app/getUCenterInfo?"
      );
      this.lwh_head_list = data.data;
      console.log(this.lwh_head_list);
    },
    // 个人信息跳转
    lwh_MyInfo() {
      this.$router.push({
        path: "/lwh_myInfo",
      });
    },
    // 优惠券跳转
    onDiscount(){
      this.$router.push("/yzMydiscount")
    },
    onClickyk() {
      this.$router.push({
        path: "/oto",
      });
    },
    onClickstudy() {
      this.$router.push({
        path: "/Wyl-my-currency",
      });
    },
    onClickcoach() {
      this.$router.push({
        path: "/lwh_york",
      });
    },
    onClickfeature() {
      this.$router.push({
        path: "/Wyl-my-study",
      });
    },
    popup() {
      this.show = true;
    },
    onlickcontact() {
      this.$router.push({
        path: "/Wyl-concern",
      });
    },
    onlicksc() {
      this.$router.push({
        path: "/Wyl-collect",
      });
    },
    //课程跳转
    onlickKc() {
      this.$router.push({
        path: "/Wyl-Kc",
      });
    },
    //会员跳转
    onlickHy() {
      this.$router.push({
        path: "/Wyl-Hy",
      });
    },
    //约客跳转
    onlickYk() {
      this.$router.push({
        path: "/Wyl-Yk",
      });
    },
    // 设置跳转
    onSetting() {
      this.$router.push("/yzSetting");
    },
    // 显示留言组件
    showLeave(){
      this.$store.commit("wsyShowLeave",true)
    },
    // 跳转到信息反馈
    toFeedback(){
      this.$router.push("/wsyFeedback")
    },
    // 跳转到我的通知
    toMessage(){
      this.$router.push("/message")
    },
    // 跳转到会员购买页面
    toVip(){
      this.$router.push("/vip")
    },
    // 跳到学习卡页面
    toCard(){
      this.$router.push("/card")
    }
  },
  computed: {},
};
</script>

<style scoped lang='scss'>
