  width: 3.6rem;
  height: 1.8rem;
  position: relative;
  /* background: yellow; */
  top: -0.8rem;
  left: 0.2rem;
  box-shadow: 0 0.01rem 0.06rem 0 rgba(0, 0, 0, 0.05);
  border-radius: 0.05rem;
  background: #fff;
  overflow: hidden;
}

.Main-top .top1 {
  width: 100%;
  height: 50%;
  background: rgb(255, 255, 255);
}
.top1 {
  padding: 0.15rem 0 0 0.15rem;
