      width: 0.15rem;
      height: 0.15rem;
    }
  }
  > .lwh_text {
    width: 0.55rem;
    height: 0.24rem;
    background-color: rgb(255, 81, 0);
    text-align: center;
    line-height: 0.24rem;
    color: #fff;
    position: absolute;
    top: 0.4rem;
    right: 0;
    font-size: 0.14rem;
    border-radius: 0.1rem 0 0 0.1rem;
  }
}
.Main-top {
  font-size: 0.2rem;
}
.Main-top .top2 {
  width: 100%;
  height: 50%;
