  justify-content: space-between;
  align-items: center;
}
.top2 {
  > p {
    width: 0.01rem;
    height: 0.54rem;
    background-color: #f5f5f5;
  }
  > div {
    width: 32%;
    text-align: center;
    height: 0.65rem;
    margin-top: 0.14rem;
    align-items: center;
    > span {
      font-size: 0.26rem;
      color: rgb(255, 81, 0);
    }
    > p:nth-of-type(1) {
      color: #595959;
    }
    > p:nth-of-type(2) {
      font-size: 0.08rem;
      color: #b7b7b7;
    }
  }
}
