  background-image: linear-gradient(45deg, rgb(253, 88, 3), rgb(255, 146, 71));
  border-radius: 0.5rem;
  margin: 0 auto;
  position: relative;
  top: -0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Main-app-img {
  width: 0.2rem;
  height: 0.17rem;
  float: left;
  margin: 0 0.1rem 0;
}
.Main-app-img img {
  width: 100%;
